body {
  font-family: 'DM Sans', sans-serif;
  

  font-size: 12px;
  line-height: 1.5;
}

.visually__hidden{
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
  }