/*кнопка меню*/
.menu-button {
  display: flex;

  align-items: center;
  width: 209px;
  height: 44px;

  margin-left: auto;
  margin-right: auto;
  margin-bottom: 33px;

  /*padding: 10px 43px;*/

  border-radius: 22px;
  border: transparent;

  background-color: $white;

  &__wrapper {
    margin-left: auto;
    margin-right: auto;
  }

  &__txt {
    margin-right: 14px;

    font-family: $secondaryFont;
    font-weight: 700;
    font-size: 16px;
    line-height: 1.3;

    color: $red;
  }

  &__icon {
    width: 10px;
    height: 10px;
  }
}
